"use client"

import { FraudRing } from "../types/forensic"

interface Props {
  rings: FraudRing[]
  onSelectRing: (ringId: string | null) => void
  selectedRing: string | null
}

export default function RingTable({
  rings,
  onSelectRing,
  selectedRing
}: Props) {
  return (
    <div className="bg-white p-6 rounded-xl shadow">
      <h2 className="text-lg font-semibold mb-4">
        Detected Fraud Rings
      </h2>

      {rings.length === 0 && (
        <p className="text-gray-500">No rings detected.</p>
      )}

      <div className="space-y-3">
        {rings.map((ring) => (
          <div
            key={ring.ring_id}
            className={`p-4 border rounded-lg cursor-pointer transition ${
              selectedRing === ring.ring_id
                ? "border-blue-500 bg-blue-50"
                : "hover:bg-gray-50"
            }`}
            onClick={() =>
              onSelectRing(
                selectedRing === ring.ring_id ? null : ring.ring_id
              )
            }
          >
            <div className="flex justify-between items-center">
              <span className="font-mono text-sm">
                {ring.ring_id}
              </span>
              <span className="text-red-600 font-semibold">
                Risk: {ring.risk_score}
              </span>
            </div>

            <div className="text-sm text-gray-600 mt-1">
              Pattern: {ring.pattern_type}
            </div>

            <div className="text-sm text-gray-600">
              Members: {ring.member_accounts.length}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
