"use client"

import { useEffect, useRef } from "react"
import Graph from "graphology"
import Sigma from "sigma"
import { ForensicResponse } from "../types/forensic"

export default function GraphView({
  data,
  onNodeClick
}: {
  data: ForensicResponse
  onNodeClick: (id: string) => void
}) {
  const containerRef = useRef<HTMLDivElement>(null)
  const sigmaRef = useRef<Sigma | null>(null)

  useEffect(() => {
    if (!containerRef.current) return

    // Kill previous instance
    if (sigmaRef.current) {
      sigmaRef.current.kill()
      sigmaRef.current = null
    }

    const graph = new Graph()

    // Add nodes
    data.suspicious_accounts.forEach((acc, index) => {
      graph.addNode(acc.account_id, {
        label: acc.account_id,
        size: 12,
        x: Math.cos(index) * 10,
        y: Math.sin(index) * 10,
        color: "#f59e0b"
      })
    })

    // Add edges
    data.fraud_rings.forEach((ring, rIndex) => {
      for (let i = 0; i < ring.members.length; i++) {
        for (let j = i + 1; j < ring.members.length; j++) {
          graph.addEdge(
            ring.members[i],
            ring.members[j],
            {
              size: 1,
              color: "#64748b"
            }
          )
        }
      }
    })

    // Create Sigma
    const renderer = new Sigma(graph, containerRef.current, {
      renderEdgeLabels: false,
      labelSize: 14,
      labelWeight: "bold"
    })

    sigmaRef.current = renderer

    // Center camera properly
    renderer.getCamera().setState({
      ratio: 1,
      x: 0,
      y: 0
    })

    // Node click
    renderer.on("clickNode", (event) => {
      const nodeId = event.node

      onNodeClick(nodeId)

      // Smooth zoom but controlled
      renderer.getCamera().animate(
        {
          x: renderer.getNodeDisplayData(nodeId)?.x ?? 0,
          y: renderer.getNodeDisplayData(nodeId)?.y ?? 0,
          ratio: 0.5
        },
        { duration: 500 }
      )
    })

    return () => {
      renderer.kill()
    }
  }, [data])

  return (
    <div
      ref={containerRef}
      style={{
        width: "100%",
        height: "100%"
      }}
    />
  )
}
